import * as React from 'react';
import { styled } from '@mui/material/styles';
import ArrowForwardIosSharpIcon from '@mui/icons-material/ArrowForwardIosSharp';
import MuiAccordion, { AccordionProps } from '@mui/material/Accordion';
import MuiAccordionSummary, {
  AccordionSummaryProps,
} from '@mui/material/AccordionSummary';
import MuiAccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';
import { toLocalTimeString } from 'src/utils/time';

const Accordion = styled((props: AccordionProps) => (
  <MuiAccordion disableGutters elevation={0} square {...props} />
))(({ theme }) => ({
  border: `1px solid ${theme.palette.divider}`,
  '&:not(:last-child)': {
    borderBottom: 0,
  },
  '&:before': {
    display: 'none',
  },
}));

const AccordionSummary = styled((props: AccordionSummaryProps) => (
  <MuiAccordionSummary
    expandIcon={<ArrowForwardIosSharpIcon sx={{ fontSize: '0.9rem' }} />}
    {...props}
  />
))(({ theme }) => ({
  backgroundColor:
    theme.palette.mode === 'dark'
      ? 'rgba(255, 255, 255, .05)'
      : 'rgba(0, 0, 0, .03)',
  flexDirection: 'row-reverse',
  '& .MuiAccordionSummary-expandIconWrapper.Mui-expanded': {
    transform: 'rotate(90deg)',
  },
  '& .MuiAccordionSummary-content': {
    marginLeft: theme.spacing(1),
  },
}));

const AccordionDetails = styled(MuiAccordionDetails)(({ theme }) => ({
  padding: theme.spacing(2),
  borderTop: '1px solid rgba(0, 0, 0, .125)',
}));

export default function CustomizedAccordions(props) {
    // 
  const [expanded, setExpanded] = React.useState<string | false>('');
  const handleChange =
    (panel: string) => (event: React.SyntheticEvent, newExpanded: boolean) => {
      setExpanded(newExpanded ? panel : false);
    };
    const file = props.props
    // 
    // ownertargetfrom
    let sourceAndTarget = ` ${localStorage.getItem("userName")}  ${file.target}`
    if(file.owner != undefined){
      // 
      if (file.state == "fromShared"){
        sourceAndTarget = ` ${file.owner} ,  ${localStorage.getItem('userName')}`
      }else{
        sourceAndTarget = ` ${file.owner} , `
      }
    }else if(file.from != undefined){
      // 
      sourceAndTarget = ` ${file.from}   ${localStorage.getItem('userName')}`
    }else if(file.target != undefined){
      // 
      sourceAndTarget = ` ${localStorage.getItem('userName')}   ${file.target}`
    }
    let remoteControl = ""
    if(file.isAllow == 1){
      remoteControl = ","
    }
  return (
    <div>
      <Accordion expanded={expanded === 'panel1'} onChange={handleChange('panel1')}>
        <AccordionSummary aria-controls="panel1d-content" id="panel1d-header">
          <Typography></Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
          {file.useLimit - file.use}
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion expanded={expanded === 'panel2'} onChange={handleChange('panel2')}>
        <AccordionSummary aria-controls="panel2d-content" id="panel2d-header">
          <Typography></Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            {toLocalTimeString(file.expire)}
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion expanded={expanded === 'panel3'} onChange={handleChange('panel3')}>
        <AccordionSummary aria-controls="panel3d-content" id="panel3d-header">
          <Typography></Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            {sourceAndTarget}
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion expanded={expanded === 'panel4'} onChange={handleChange('panel4')}>
        <AccordionSummary aria-controls="panel3d-content" id="panel3d-header">
          <Typography></Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            {remoteControl}
          </Typography>
        </AccordionDetails>
      </Accordion>
    </div>
  );
}